

.app-container {
  max-width: 500px;
  margin: auto;
  text-align: center;
  font-family: Arial, sans-serif;
  color: #333;
  
}

.add-edit-form {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.todo-input {
  width: 75%;
  padding: 8px;
  font-size: 16px;
}

.todo-button {
  padding: 8px;
  font-size: 16px;
  cursor: pointer;
  transition: 2s;
  background-color: #4caf50;
}

.todo-list {
  margin-top: 20px;
}

.todo-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  border: 1px solid #ddd;
  margin-bottom: 10px;
  border-radius: 4px;
  background-color: #f9f9f9;
}

.edit-button,
.delete-button {
  margin-left: 5px;
  cursor: pointer;
}

.edit-button {
  background-color: #4caf50;
  color: #fff;
  border: none;
  padding: 5px;
  border-radius: 4px;
}

.delete-button {
  background-color: #f44336;
  color: #fff;
  border: none;
  padding: 5px;
  border-radius: 4px;
}
